body {
    margin: 0;
    padding: 0;
}
#map {
    position: absolute;
    top: 10;
    bottom: 10;
    width: 100%;
}

.map-container {
    height: 400px;
}

.sidebar {
    background-color: #5670ff;
    color: #FFFFFF;
    padding: 6px 15px;
    font-family: monospace;
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    margin: 12px;
    border-radius: 4px;
}

#instructions {
    position: absolute;
    margin: 20px;
    width: 25%;
    top: 0;
    bottom: 20%;
    padding: 20px;
    background-color: #fff;
    overflow-y: scroll;
    font-family: sans-serif;
}

